import React from "react";
import {Link} from "react-router-dom"
const Navbar = () => {
  return (
    <div className="grid">
      <header>
    <h1>spider man</h1>
        <nav>
          <ul>
            <li>
              <Link to="/">Home</Link>
            </li>
            <li>
              <Link to="/info">spiderman info</Link>
            </li>
            <li>
              <Link to="/images">images</Link>
            </li>
          </ul>
        </nav>
          </header>
         
    </div>
  );
};

export default Navbar;
